'use client';
import { useState, useEffect } from 'react';
import InvestmentCard from '../../components/InvestmentCard';

export default function Investments() {
    const [plans, setPlans] = useState([]);

    useEffect(() => {
        // In a real app, we would fetch from the API
        // fetch('/api/investments').then(res => res.json()).then(data => setPlans(data));

        // For now, since we can't run the server to fetch from our own API route easily without CORS/setup issues in this specific environment if not running,
        // we will just use the same data here or try to fetch.
        // Let's try to fetch, but provide fallback.

        const fetchPlans = async () => {
            try {
                const res = await fetch('/api/investments');
                if (res.ok) {
                    const data = await res.json();
                    setPlans(data);
                } else {
                    throw new Error('Failed to fetch');
                }
            } catch (e) {
                // Fallback data if API fails (e.g. server not running)
                setPlans([
                    {
                        id: 1,
                        name: 'Model 3 Fund',
                        minInvestment: 500,
                        roi: '5-7%',
                        duration: '12 months',
                        risk: 'Low',
                        description: 'Stable investment focused on mass market vehicle production.'
                    },
                    {
                        id: 2,
                        name: 'Cybertruck Growth',
                        minInvestment: 1000,
                        roi: '8-12%',
                        duration: '24 months',
                        risk: 'Medium',
                        description: 'Growth-oriented fund backing the expansion of Cybertruck manufacturing.'
                    },
                    {
                        id: 3,
                        name: 'Supercharger Network',
                        minInvestment: 2500,
                        roi: '6-9%',
                        duration: '18 months',
                        risk: 'Low-Medium',
                        description: 'Infrastructure investment in the global charging network.'
                    },
                    {
                        id: 4,
                        name: 'Autonomy AI',
                        minInvestment: 5000,
                        roi: '15-25%',
                        duration: '36 months',
                        risk: 'High',
                        description: 'High-risk, high-reward investment in Full Self-Driving technology.'
                    }
                ]);
            }
        };

        fetchPlans();
    }, []);

    return (
        <div style={{ padding: '6rem 2rem 4rem' }}>
            <h1 style={{ fontSize: '2.5rem', marginBottom: '1rem', textAlign: 'center' }}>Investment Plans</h1>
            <p style={{ textAlign: 'center', color: 'var(--tesla-gray)', marginBottom: '3rem' }}>
                Choose a plan that suits your financial goals and risk tolerance.
            </p>

            <div style={{
                display: 'grid',
                gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))',
                gap: '2rem',
                maxWidth: '1200px',
                margin: '0 auto'
            }}>
                {plans.map(plan => (
                    <InvestmentCard key={plan.id} plan={plan} />
                ))}
            </div>
        </div>
    )
}
